import { Node, Document, Block } from '@ali/4ever-cangjie';

const isTaskNode = (node: Node) => Block.isBlock(node) && node.type === 'paragraph' && node?.data?.list?.isTaskList;

export function isNodeInTask(node: Node | null, document: Document) {
  if (!node) return false;

  return !!document.getClosest(node.key, isTaskNode);
}
